import React, {useState, useEffect} from "react";
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome'
import * as icons from '@fortawesome/free-solid-svg-icons'
import 'bootstrap/dist/css/bootstrap.min.css';
import "./App.css";
import {rand} from './util/util';

import fonts from './util/fonts';


function SlotMachine() {

    const [slot1, setSlot1] = useState(fonts[0]);
    const [slot2, setSlot2] = useState(fonts[0]);
    const [slot3, setSlot3] = useState(fonts[0]);
    const [score, setScore] = useState(0);
    const [roundEnded, setRoundEnded] = useState(false);

    const [machineOn, setMachineOn] = useState(false);

    const helper = (index) => setInterval(() => {

        const ico = fonts[rand(fonts.length)];

        switch (index) {
            case 0:
                setSlot1(ico);
                break;
            case 1:
                setSlot2(ico);
                break;
            case 2:
                setSlot3(ico);
                break;
        }

    }, 150);

    const startMachine = () => {

        if (machineOn) {
            return;
        }

        setSlot1(fonts[0]);
        setSlot2(fonts[0]);
        setSlot3(fonts[0]);
        setMachineOn(true);
        setRoundEnded(false);

        let interval = helper(0);

        setTimeout(() => {

            clearInterval(interval);

            interval = helper(1);

            setTimeout(() => {

                clearInterval(interval);

                interval = helper(2);

                setTimeout(() => {

                    clearInterval(interval);

                    setRoundEnded(true);
                    setMachineOn(false);

                }, 2000);


            }, 2000)

        }, 2000)


    };

    const calcScore = () => {

        const arr = [slot1, slot2, slot3].sort();

        if(slot1 === slot2 === slot3){
            setScore(score + 2);
        }else if(slot1 === slot2 || slot1 === slot3){
            setScore(score + 1);
        }else if(slot3 === slot2 || slot3 === slot1){
            setScore(score + 1);
        }

    };

    useEffect(() => {

        if (roundEnded) {

            calcScore();

        }


    }, [roundEnded]);


    return (
        <div id="slot-machine" className="mt-5">

            <div className="card mx-auto" style={{width: '500px'}}>

                <div className="card-header">
                    <span>Score : {score}</span>
                </div>

                <div className="card-body">

                    <div className="row text-center">

                        <div className="col-md-4 ">
                            <FontAwesomeIcon icon={icons[slot1]} color="skyblue" size="5x"/>
                        </div>
                        <div className="col-md-4 ">
                            <FontAwesomeIcon icon={icons[slot2]} color="skyblue" size="5x"/>
                        </div>
                        <div className="col-md-4">
                            <FontAwesomeIcon icon={icons[slot3]} color="skyblue" size="5x"/>
                        </div>

                    </div>
                </div>

                <div className="card-footer">
                    <div className="text-center">
                        <button type="button" className="btn btn-warning px-5" onClick={startMachine}
                                disabled={machineOn}>Start
                        </button>

                    </div>

                </div>
            </div>
        </div>

    );

}


function App() {

    return (
        <div className="container">

            <h2 className="text-center "><u>Slot Machine</u></h2>
            <SlotMachine/>

        </div>);
}

export default App;
